<template>
    <div id="index" class="echart">
        <div id="myChart"></div>
    </div>
</template>
<script>

export default {
    name: 'hello',
    data() {
        return {
            echartData: [],
            langType: "cn",
        }
    },
    mounted() {
        this.toggleLang();
        this.getProfessionalData();
    },
    watch: {
        "$route.fullPath": {
            handler() {
                this.toggleLang();
                this.getProfessionalData();
            }
        }
    },
    methods: {
        drawLine() {
            // 基于准备好的dom，初始化echarts实例
            const _this = this;
            const chartBox = document.getElementById('myChart');
            const size = chartBox.clientWidth;
            const width = size + "px";
            const height = size + "px";
            let myChart = this.$echarts.init(chartBox, null, {
                width,
                height,
            });
            // 绘制图表
            myChart.setOption({
                tooltip: {
                    trigger: 'item',
                    backgroundColor: "#dadada",
                    textStyle: {
                        color: "#000",
                    },
                    formatter(params) {
                        const html = '<b>' + params.name + '：</b>' + params.value;
                        return html;
                    }
                },
                legend: {
                    show: true,
                    bottom:0,
                    borderRadius:0,
                    itemHeight:25
                },
                color: ["#00a0e9", "#005982", "#5d9cec", "#7ecef4"],
                series: [{
                    name: '职称统计',
                    type: 'pie',
                    radius: ['0', '86%'],
                    center: ["50%", "43%"],
                    avoidLabelOverlap: false,
                    hoverAnimation: false,
                    minAngle: 30,
                    label: {
                        show: true,
                        color: "#fff",
                        position: "inside",
                        textStyle: {
                            fontSize: '16',
                        },
                        rotate: 90
                    },
                    labelLine: {
                        show: false
                    },
                    data: this.echartData
                }]
            });
            myChart.on("click", function (params) {
                var workkey = params.data.workCode;
                var name = params.data.name;
                var vuexParams = {
                    workkey: workkey,
                    name: name
                };
                var params = {
                    workTitles: workkey,
                    size: 12,
                    page: 1
                };
                _this.$router.push({ name: _this.langType != "cn" ? _this.langType + 'search' : "search" });
                _this.$store.commit('clearkeySerach'); //清空关键字 
                _this.$store.commit('setflg', false); //标记
                _this.$store.commit("setworkTitlesparams", vuexParams); //参数给vuex
                _this.$store.commit("search_More", params);
            });
            myChart.dispatchAction({ type: 'highlight', seriesIndex: 0, dataIndex: 0 });
        },
        getProfessionalData() {
            const _this = this;
            this.$store.commit('fetchloading', true)
            this.commonMethods.emitAjax({
                path: "/api/front/psons/count/title",
                success(response) {
                    _this.$store.commit('fetchloading', false)
                    _this.echartData = [];
                    for (let index = 0; index < response.length; index++) {
                        const data = response[index];
                        _this.echartData.push({
                            value: data.count,
                            name: data.work_title,
                            workCode: data.work_title_code
                        })
                    }
                    _this.drawLine();
                }
            })
        },
        toggleLang() {
            const _this = this;
            this.commonMethods.selectLangType(function (langType) {
                _this.langType = langType;
            })
        }
    }
}
</script>




